
.signout-button {
    color: #df0b0b; /* Set the text color */
    padding: 10px 20px; /* Set padding around the button text */
    border: none; /* Remove the button border */
    cursor: pointer; /* Change cursor to pointer on hover */
    border-radius: 5px; /* Add rounded corners to the button */
    font-size: 16px; /* Set the font size */
    transition: background-color 0.3s ease-in-out; /* Add a smooth hover effect */
    position: absolute;
    top:10px;
    right: 10px;
  }
  
  .signout-button:hover {
    background-color: #e529d5; /* Change background color on hover */
  }

  .signoutbutton{
    color: rgb(250, 248, 248);
    background-color: rgb(118, 118, 118);
  }