.button1 {
  margin: 10px;
  padding: 15px 30px;
  text-align: center;
  text-transform: uppercase;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;
  border-radius: 10px;
  display: block;
  border: 0px;
  font-size: 20px;
  font-weight: 700;
  box-shadow: 0px 0px 14px -7px #0beb64;
  background-image: linear-gradient(45deg, #67cd1f 0%, #19f059 51%, #FF512F 100%);
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}