.sign-in-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-image: url(./bg3.avif);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#errorMessage{
  color: red;

}

form {
  background-color:rgb(0,0,0);
  background-color: rgba(0,0,0, 0.4); 
  padding: 50px;
  border-radius: 1px;
  box-shadow: 0px 0px 0px 0px  #6d6c6d;
  border: 0px solid #615e61;
  text-align: center;
  color: white;
  font-weight: bold;
  border: 3px solid #f1f1f1;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  text-align: center;
}

h1 {
  font-size: 24px;
  margin-bottom: 20px;
}

label {
  font-weight: bold;
}

input {
  width: 50%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}
#isForgotPassword{
  background-color: #c505e7;
  margin-right: 20px;
  
}
#isRegister{
  background-color: #db2323;
  margin-left: 20px;
  margin-top: 20px;
}

button:hover {
  background-color: #050363;
}
